<template>
  <div class="detail-container">
    <div class="post-container" v-if="postHtml">
      <article class="post-content">
        <header class="post-header">
          <h1 class="post-title">{{ name }}</h1>
        </header>
        <section class="markdown-body post-body" v-html="postHtml"></section>
      </article>
      <comment :title="name" />
      <!-- <comm/> -->
    </div>
  </div>
</template>

<script>
import Comment from "@/components/comment";
import Comm from "@/components/comm";
import { setPageTitle } from "@/utils/page-title-util";
import { getToken } from "@/utils/cookie-util";
// import { giteeApi } from "@/utils/gitee-api";
import { githubApi } from "@/utils/github-api";
import VditorPreview from "vditor/dist/method.min";

export default {
  components: {
    Comment,
    Comm,
  },
  data() {
    return {
      name: this.$route.params.name,
      postHtml: "",
    };
  },
  async mounted() {
    if (this.name) {
      setPageTitle(this.name);
      this.getPostHtml();
    }
  },
  methods: {
    async getPostHtml() {
      let postContent = "";
      this.$loading.show();
      const file = await githubApi.getTextRawFile(`db/_post/list/${this.name}.md`);
      this.$loading.hide();
      if (!file) {
        return (this.postHtml = "");
      }
      postContent = file.content;
      // const [err, res] = await this.$to(
      //   this.$axios.post("https://gitee.com/api/v5/markdown", {
      //     text: postContent,
      //     access_token: getToken(),
      //   })
      // );
      // if (err) {
      //   return (this.postHtml = "");
      // }
      // this.postHtml = file;
      VditorPreview.md2html(file)
        .then((data) => {
          this.postHtml = data;
        })
        .catch((e) => {
          this.postHtml = e;
        });
    },
  },
};
</script>

</script>
<style lang="scss" scoped>
.detail-container {
  width: 100%;
  min-height: 100%;
  background: var(--bg-color);
  padding: 20px 0;
  .post-container {
    width: 100%;
    max-width: 1170px;
    background: var(--front-color);
    margin: 0 auto;
    .post-content {
      padding: 50px;
      position: relative;
      min-height: calc(100vh - 360px);
      &::before {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        width: 5px;
        height: 80px;
        background: var(--main-color);
      }
      .post-header {
        margin-bottom: 30px;
        text-align: center;
        .post-title {
          font-size: 30px;
          color: var(--main-color);
          padding-bottom: 20px;
          border-bottom: 1px solid var(--light-color);
        }
      }
    }
  }
}
</style>
